/* tailwind css */
@tailwind base;
@tailwind components;
@tailwind utilities;
/* font */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display+SC:wght@700&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth
}
/* //card */
.card {
  transition: transform 0.3s ease;
}

.card:hover {
  transform: scale(1.1);
}

/* navbar */
.navbar{
  font-family: 'Poppins', 'sans-serif';
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
box-shadow: 0px 8px 6px -6px gray;
--webkit-box-shadow:0px 8px 6px -6px gray;
border-bottom: 0.5px solid gray !important;
}
.nav-link{
  font-weight: 300 !important;
}
.active{
  border-bottom: 1px solid black;
}
.navbar-brand{
  font-family: 'roboto', sans-serif;
  font-weight: 700;
  letter-spacing: 3px;
}
.footer a{
text-decoration: none;
color: white;
font-size: 18px;
padding-left: 10px;
}
.footer a:hover{
  color: brown;
}
.pnf{
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
min-height: 65vh;

}
.pnf-title{
  font-size: 100px;
  font-weight: 700;
}
.pnf-heading{
  font-weight: normal;
}
.pnf-btn {
  text-decoration: none;
  color: black;
  border: 1px solid black;
  padding: 10px;
  margin-top: 10px;
}


.form-container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 65vh;

}

.form-container{
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  margin:  30px 0px;


}
.Register-form{
  border: 1px solid gray;
  width: 30%;
  padding: 30px;
}
.login-form{
  border: 1px solid gray;
  width: 30%;
  padding: 30px;
  font-family: cursive;
}
@media only screen and (max-width:768px) {
  .btn{
      width: 100%;
  }
  .form-container{
    min-height: 65%;
  }
  .Register-form{
      width: 100%;
     
  }
  .login-form{
    width: 100%;
  }
}
p{
  text-align: justify;
}
